import Vue from 'vue';
import VueRouter from 'vue-router'

//
import Home from '@/views/Home/Home.vue'   //@src
import Register from '@/views/Register'
import Search from '@/views/Search/index.vue'
import Login from '@/views/Login'
import Detail from '@/views/Detail'
import AddCartSuccess from '@/views/AddCartSuccess'
import ShopCart from '@/views/ShopCart'
import Trade from '@/views/Trade'
import Pay from '@/views/Pay'
import PaySuccess from '@/views/PaySuccess'
import Center from '@/views/Center'

//
import MyOrder from "@/views/Center/MyOrder"
import GroupOrder from "@/views/Center/GroupOrder"

import store from "@/store"  //

let router = new VueRouter({  //
    mode: 'hash',
    routes: [
        {
            path: '/home',
            component: Home,
            meta: { show: true }
        },
        {
            path: '/search/:keyword',
            component: Search,
            meta: { show: true },  //
            name: 'search'
        },
        {
            path: '/login',
            component: Login,
            meta: { show: false },
        },
        {
            path: '/register',
            component: Register,
            meta: { show: false },
        },
        {
            path: '/detail/:skuId',  //
            component: Detail,
            meta: { show: true },
        },
        {
            //
            path: '/addcartsuccess',
            component: AddCartSuccess,
            name: "addCartSuccess",
            meta: { show: true },
        },
        {
            //
            path: '/shopcart',
            component: ShopCart,
            name: "shopCart",
            meta: { show: true },
        },
        {
            //
            path: '/trade',
            component: Trade,
            name: "trade",
            meta: { show: true },
            //
            beforeEnter:(to,from,next)=>{
                    if(from.path=='/shopcart'){  //
                        next();
                    }else{
                        next(false);  //
                    }
            }
        },
        {
            //
            path: '/pay',
            component: Pay,
            name: "pay",
            meta: { show: true },
            beforeEnter:(to,from,next)=>{
                        if(from.path=='/trade'){  //
                            next();
                        }else{
                            next(false);  //
                        }
                }
           

        },
        {
            //
            path: '/paysuccess',
            component: PaySuccess,
            name: "paysuccess",
            meta: { show: true },

        },
        { //
            path: '/center',
            component: Center,
            name: "Center",
            meta: { show: true },
            children: [
                {
                    path: 'myorder',  //
                    component: MyOrder
                },
                {
                    path: 'grouporder',
                    component: GroupOrder
                },
                {
                    path:'/center',
                    redirect:'/center/myorder'   //
                }
            ]
        },
        //home
        {
            path: '*',
            redirect: '/home'
        },

    ],
    //
    scrollBehavior(to, from, savedPosition) {
        return { y: 0 }
    }
})
//
router.beforeEach(async (to, from, next) => {
    // to
    // from 
    // next:  next(path) 
    let token = store.state.user.token //
    let name = store.state.user.userInfo.name;   //

    if (token) {
        if (to.path == "/login") { //tokenlogin
            next('/home')  ///home
            console.log("home")
        } else {
            //login
            if (name) {    //token
                next();   // 
            } else {   //
                try {
                    await store.dispatch("getUserInfo");  //token
                    console.log("")
                    next();   //
                } catch (error) {
                    //token
                    //token
                    console.log("token")
                    await store.dispatch('userLogOut');
                    localStorage.removeItem("")
                    next('/login');
                }
            }
        }
    } else {   //token

        // let toPath=to.path;
        // if (toPath == '/home'||toPath=='/search') {    
        //     next()  //
        // } else {
        //     //
        //     //console.log("")
        //     // next('/login?redirect='+toPath);  //,
        //     next('/login');
        // }
        if (to.path == "/shopcart") {   //
            console.log("")
            next('/login')  //
        } else {
            //
            next();  //
        }
//   console.log("")
//         next('/login');


    }


})


export default router
