import {v4 as uuidv4} from 'uuid' 
// 
export const getUUID = () =>{
    let uuid_token = localStorage.getItem('UUIDDTOKEN');   //

    if(!uuid_token){ //uuid
        uuid_token=uuidv4(); //
        console.log("new token")

        localStorage.setItem('UUIDTOKEN',uuid_token);//
        
    }
    return uuid_token  // uuid    
}