import { reqCartList,reqDeleteCartById ,reqUpdateCheckCartById} from "@/api"

//

//actions
const actions = {
    async getCartList({commit}){
        let result =await reqCartList();
        console.log("get CartList");
        if(result.code==200){
            console.log("CartList" + result.data);
             commit("GETCARTLIST",result.data)
        }
    },
    
    async deleteCartListBySkuId({commit},skuId){
        let result =await reqDeleteCartById(skuId);
        console.log("delete send");
        if(result.code==200){
            console.log("delete" + result.data);
            //data
             //
             return 'ok'
        }
        else{
            return Promise.reject(new Error('failed'));
        }
    },
    
    async updateCheckedById({commit},{skuId,isChecked} ){
        let result =await reqUpdateCheckCartById(skuId,isChecked);
        console.log("update send");
        if(result.code==200){
            console.log("update" + result.data);
            //data
             //
             return 'ok'
        }
        else{
            return Promise.reject(new Error('failed'));
        }
    },
    
    //
    deleteAllCheckedCart({dispatch,getters}){
        //contextcommitmutationsstategettersdispatchactionstate
        //gettersstate
        let promiseAll=[]
        getters.cartList.cartInfoList.forEach(item=>{
           // if(item.isChecked) dispatch("deleteCartListBySkuId",item.skuId);
           let promise = item.isChecked==1?dispatch("deleteCartListBySkuId",item.skuId):'';   //promise
           promiseAll.push(promise)
        });
        //Promise.all([p1,p2,p3]),p1p2promise
        return Promise.all(promiseAll);
    },
    //
    updateAllCartIsChecked({dispatch,state},isChecked){
        let promiseAll=[];
        state.cartList[0].cartInfoList.forEach(item=>{
            let promise = dispatch('updateCheckedById',{skuId:item.skuId,isChecked:isChecked})
            promiseAll.push(promise)
        });
        console.log("updateCheckedById")
        return Promise.all(promiseAll);
    }
}
//mutationsstate
const mutations = {
    GETCARTLIST(state,cartList){
        state.cartList = cartList
    }
}
//state
const state = {
    cartList:[]

}
//
const getters = {
    cartList(state){
        return state.cartList[0]||{}
    }
}

//store
export default ({
	actions,
	mutations,
	state,
    getters 
})